import React, { Component } from "react";
import { Query } from "react-apollo";
import gql from "graphql-tag";

const ALL_ITEMS_QUERY = gql`
  query ALL_ITEMS_QUERY {
    items {
      id
      title
      price
      description
      image
      largeImage
    }
  }
`;

class Items extends Component {
  render() {
    return (
      <div>
        <p>Items!</p>
        <Query query={ALL_ITEMS_QUERY} ssr={true}>
          {({ data, error, loading }) => {
            return (
              <div>
                {error && <p>We got an error</p>}
                {loading && <p>We load a data for you</p>}
                {data && (
                  <ul>
                    {data.items.map(item => (
                      <li key={item.id}>
                        <div>{item.id}</div>
                        <div>{item.title}</div>
                        <div>{item.description}</div>
                        <div>{item.image}</div>
                        <div>{item.largeImage}</div>
                      </li>
                    ))}
                  </ul>
                )}
              </div>
            );
          }}
        </Query>
      </div>
    );
  }
}

export default Items;
